body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* @import 'antd/dist/antd.css'; */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.top-nav{
    width: 100%;
    background: #011F5B;
    color: #FFFFFF;
    font-size: 14px;
    padding: 5px;
}

.top-nav .top-bar-info{    
    list-style: none;
    padding: 0;
    margin-top: 8px;
    display: flex;
    text-align: center;
}

.top-nav .top-bar-info .phone{
    text-indent: 3px;
    margin-right: 20px;
    position: relative;
    display: inline;
}
.top-nav .top-bar-info .top-bar-hour{
    margin-left:10px;
    position: relative;
    display: inline;
}
.top-nav .top-bar-icons{    
    width: 250px;    
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 10px;
    margin: auto;
    text-align: center;
}
.top-nav .top-bar-icons .icon a{
    list-style: none;
    border-radius: 50px;
    background-color: whitesmoke;
    width: 20px;
    height: 20px;
    padding: 5px 7px;
    color: black;

}
.container .bottom-nav{
    background-color: white;
    width: 100%;
    height: 100px;
    text-align: left;
    display: flex;
    align-items: center;
}

.container .bottom-nav img{
    width: 180px;
    padding: 5px;
    margin-left: 100px;
}
.container .bottom-nav .drawar{
    display: none;
}

@media only screen and (max-width: 500px) {
    body {
      background-color: lightblue;
    }
    .top-nav .top-bar-info{    
        flex-direction: column;
    }
    .container .bottom-nav img{
        margin-left: 20px;
    }
    .container .bottom-nav .drawar{
        display: block;
        margin-left: 40px;
        font-size: 30px;
    }
  }
