*{
    margin: 0%;
    padding: 0;
    box-sizing: border-box;

}

.section-about .aboutimage_style{
  width: 100%;
  height: 80%;
}

.section-about .about-us-info{
    margin-top: 72px;
    margin-bottom: 60px;
    font-size: 20px;
    padding: 60px;
    text-align: left;
}

.section-about .about-us-info h1{
    font-weight: 900;
    font-size: 70px;
    line-height: 70px;
    font-family: 'Lato', sans-serif;
    color: #011F5B;
    letter-spacing: 0.03em;
}
.text-curly {
    font-family: 'Playfair Display', serif;
    font-style: italic;
}

.section-about .about-us-info p,.about-btn  {
    font-family: 'Lato', sans-serif;
    font-size: 18px;
    line-height: 35px;
    font-weight: 400;
    margin: 0 0 12px;
    padding: 0;
    letter-spacing: 0.03em;
}


.about-btn .btn-theme {
    border: 0px;
    background: #011F5B;
    color: #837b7b;
    display: inline-block;
    font-size: 16px;
    line-height: 32px;
    font-weight: 600;
    padding: 16px 36px;
    position: relative;
    text-transform: uppercase;
    border-radius: 50px;
}
.about-btn .btn-theme:hover{
    background: #073f55;
    color: #FFFFFF;
}
/* About-top End----------------- */

/* OUR VISION & MISSION Start---------- */
.about-us-info-bottom {
    text-align: left;
}
.about-us-info-bottom h1 {
    position: relative;
    font-size: 36px;
    margin-bottom: 0px;
    font-family: 'Playfair Display', serif;
    letter-spacing: 0.03em;
    font-weight: 900;
    display: inline-block;
    color: #011F5B;
}
.about-us-info-bottom p{
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    line-height: 28px;
    font-weight: 400;
    margin: 0 0 12px;
    padding: 0;
    letter-spacing: 0.03em;
}
.service-lists {
    position: relative;
    float: left;
    display: block;
    padding-top: 24px;
    clear: both;
}
.service-lists li{
    list-style: none;
}

.service-lists li .service-icon1{  
    transition: all 0.5s ease-out;
    background: #FFFFFF;
    color: #011F5B;
    border: 2px solid #CCCCCC;
    height: 60px;    
    border-radius: 100%;   
    background-clip: padding-box;
    line-height: 54px;
    left: 0px;
    position: absolute;
    top: 40px;
    width: 60px;
    text-align: center;
    font-size: 24px;
}
.service-lists li .service-icon2{  
    transition: all 0.5s ease-out;
    background: #FFFFFF;
    color: #011F5B;
    border: 2px solid #CCCCCC;
    height: 60px;    
    border-radius: 100%;   
    background-clip: padding-box;
    line-height: 54px;
    left: 0px;
    position: absolute;
    top: 180px;
    width: 60px;
    text-align: center;
    font-size: 24px;
}

.service-lists li .service-text {
    padding-left: 84px;
    position: relative;
}
.service-lists li .service-text span {
    color: #011f5b;
    position: relative;
    display: block;
    padding-top: 6px;
    margin-bottom: 16px;
    font-size: 24px;
    font-weight: 600;
}
.service-lists li .service-text p {
    color: #000000;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    line-height: 28px;
    font-weight: 400;
    margin: 0 0 12px;
    padding: 0;
    letter-spacing: 0.03em;
}



/* OUR VISION & MISSION End---------- */

@media only screen and (max-width: 600px) {
    .section-about .about-us-info{
        margin-top: 10px;
