.counter-section{
width: 100%;
}
.counter-cantainer {
    background-image: url(/Avyaja_project2/static/media/counter_bg.ab35ef23.jpg);
    background-repeat: no-repeat;
    background-position: 0 70%;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    overflow: hidden;
    padding: 10px 10px;
}

.counter_heading h1{
    position: relative;
    font-size: 50px;
    margin:30px 0 32px 0;
    font-family: 'Playfair Display', serif;
    letter-spacing: 0.03em;
    font-weight: 900;
    display: inline-block;
    color: #011F5B;   
}

.counter_heading p{
    font-size: 25px;
    line-height: 32px;
    font-weight: 500;
    font-family: 'Lato', sans-serif;
    color: #000000;
    margin-bottom: 12px;
    padding: 0px 0 0 24px;
    word-spacing: 0.03em;
    letter-spacing: 0px;
}
.card-container{
    padding: 10px;
    align-items: center;
}
.card-container .card-title{
    text-align: center;
